body {
  background-color: rgba(0, 0, 0, .647);
}

p {
  margin: 0;
}

button {
  cursor: pointer;
  width: 100px;
  height: 45px;
  border-radius: 10px;
  transition-property: background-color;
  transition-duration: .3s;
}

.btn_top {
  width: 80px;
  height: 80px;
  background-color: rgba(0, 0, 0, .12);
  border: 1px solid rgba(0, 0, 0, .596);
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  transition-property: background-color;
  transition-duration: .3s;
  display: flex;
  position: fixed;
  bottom: 100px;
  right: 30px;
}

.search-svg {
  fill: #ffebcd;
  width: 40px;
  height: 40px;
}

img:hover {
  transform: scale(1.04);
}

.btn_top:hover, button:hover {
  background-color: rgba(137, 43, 226, .325);
}

li {
  list-style: none;
}

ul {
  padding: 0;
}

img {
  cursor: pointer;
  width: 100%;
  height: 250px;
  transition: transform .3s;
}

.gallery {
  color: #000;
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  padding: 0 20px;
  font-size: 16px;
  display: flex;
}

.wrapp_input {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.list_item {
  width: 350px;
  height: 250px;
}

video {
  height: 220px;
  width: 100%;
  cursor: pointer;
}

input {
  width: 300px;
  height: 40px;
  border-radius: 10px;
  padding-left: 10px;
  font-size: 14px;
}

@media screen and (min-width: 480px) {
  .btn_top {
    width: 100px;
    height: 100px;
  }

  .search-svg {
    width: 60px;
    height: 60px;
  }
}

.is-hidden {
  opacity: 0;
  display: none;
  overflow: hidden;
}

.photo-card {
  width: 100%;
  cursor: pointer;
  background-color: #fff;
  border-radius: 0 0 4px 4px;
  transition-property: transform;
  transition-duration: .3s;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 440px) {
  .photo-card {
    width: 350px;
  }
}

.photo-card:hover {
  transform: scale(1.02);
}

.info {
  justify-content: center;
  align-items: center;
